import { useReducer, useEffect, useState } from "react";
import io from "socket.io-client";
import { ThemeProvider } from "@mui/material/styles";
import {
  Button,
  TextField,
  Typography,
  Dialog,
  DialogTitle,
  DialogContent,
  Card,
  CardContent,
  FormControl,
  FormControlLabel,
  RadioGroup,
  Radio,
  AppBar,
  Toolbar,
  IconButton,
} from "@mui/material";
import Accessibility from "@mui/icons-material/Accessibility";
import AccountBox from "@mui/icons-material/AccountBox";
import "./App.css";
import MessageBubbleList from "./messagebubblelist";
import theme from "../theme";
import logo from "./Chatroom.png";
const Chatroom = () => {
  const initialState = {
    messages: [],
    status: "",
    chatNameStatus: "enter a chat name",
    chatNameError: true,
    roomNameStatus: "enter a roomname",
    roomNameError: true,
    selectedRoom: "",
    showjoinfields: true,
    alreadyexists: false,
    chatName: "",
    roomName: "",
    typingMsg: "",
    message: "",
    isTyping: false,
    inRoom: false,
    users: [],
    rooms: [],
  };
  const reducer = (state, newState) => ({ ...state, ...newState });
  const [state, setState] = useReducer(reducer, initialState);
  const [dialogOpen, setDialogOpen] = useState(false);
  const handleOpenDialog = () => {
    setDialogOpen(true);
  };
  const handleCloseDialog = () => setDialogOpen(false);
  useEffect(() => {
    serverConnect();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const serverConnect = () => {
    //connect to server
    const socket = io.connect("localhost:5000", {
      forceNew: true,
      transports: ["websocket"],
      autoConnect: true,
      reconnection: false,
      timeout: 5000,
    });
    // const socket = io.connect();
    socket.on("nameexists", onExists);
    socket.on("welcome", addMessage);
    socket.on("someonejoined", addMessage);
    socket.on("someoneleft", addMessage);
    socket.on("someoneistyping", onTyping);
    socket.on("newmessage", onNewMessage);
    socket.on("roomsandusers", onDataUpdate);
    setState({ socket: socket });
    socket.emit("getDataUpdate");
  };
  const onDataUpdate = (dataFromServer) => {
    setState({
      users: dataFromServer,
      rooms: dataFromServer.map((user) => user.room),
    });
  };
  const onNewMessage = (dataFromServer) => {
    addMessage(dataFromServer);
    setState({ typingMsg: "" });
  };
  const onTyping = (dataFromServer) => {
    if (dataFromServer.from !== state.chatName) {
      setState({
        typingMsg: "..." + dataFromServer.text,
      });
    }
  };
  const onExists = (dataFromServer) => {
    setState({ chatNameStatus: dataFromServer.text, chatNameError: true });
  };
  // generic handler for all other messages:
  const addMessage = (dataFromServer) => {
    let messages = state.messages;
    messages.push(dataFromServer);
    setState({
      messages: messages,
      showjoinfields: false,
      inRoom: true,
      alreadyexists: false,
    });
  };

  // handler for join button click
  const handleJoin = () => {
    state.socket.emit("join", {
      chatName: state.chatName,
      roomName: state.roomName,
    });
  };
  // handler for name TextField entry
  const onNameChange = (e) => {
    setState({ chatName: e.target.value, chatNameStatus: "enter a chat name" });
    if (e.target.value !== "") {
      setState({ chatNameError: false });
    } else {
      setState({ chatNameError: true });
    }
  };
  // handler for room TextField entry
  const onRoomChange = (e) => {
    setState({
      roomName: e.target.value,
      roomNameStatus: "enter a room name",
      selectedRoom: "",
    });
    if (e.target.value !== "") {
      setState({ roomNameError: false });
    } else {
      setState({ roomNameError: true });
    }
  };

  // keypress handler for message TextField
  const onMessageChange = (e) => {
    setState({ message: e.target.value });
    if (state.isTyping === false) {
      state.socket.emit("typing", { from: state.chatName }, (err) => {});
      setState({ isTyping: true }); // flag first byte only
    }
  };
  // enter key handler to send message
  const handleSendMessage = (e) => {
    if (state.message !== "") {
      state.socket.emit(
        "message",
        { from: state.chatName, text: state.message, room: state.roomName },
        (err) => {}
      );
      setState({ isTyping: false, message: "" });
    }
  };

  const radioGroupChange = (event) => {
    setState({
      selectedRoom: event.target.value,
      roomName: event.target.value,
      roomNameError: false,
    });
  };

  function uniqueFilter(value, index, self) {
    return self.indexOf(value) === index;
  }

  return (
    <ThemeProvider theme={theme}>
      <AppBar position="static">
        <Toolbar color="primary" title="Sample Toolbar">
          <Typography variant="h6" color="inherit">
            Chat It Up - Info 3139
          </Typography>
          <section style={{ height: 90, width: 90, marginLeft: "auto" }}>
            <IconButton onClick={handleOpenDialog}>
              {state.inRoom && (
                <Accessibility
                  style={{ color: "white", height: 70, width: 70 }}
                />
              )}
            </IconButton>
          </section>
        </Toolbar>
      </AppBar>

      {state.showjoinfields && (
        <div>
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              marginTop: "5%",
            }}
          >
            <img src={logo} alt="Logo" height={80} width={80} />
          </div>
          <Typography
            variant="h6"
            color="inherit"
            style={{ textAlign: "center" }}
          >
            Sign in
          </Typography>
          <Card variant="outlined">
            <Card variant="outlined" style={{ margin: "2%", padding: "1%" }}>
              <CardContent>
                <TextField
                  onChange={onNameChange}
                  placeholder="Chat Name"
                  autoFocus={true}
                  required
                  value={state.chatName}
                  error={state.chatNameError}
                  helperText={state.chatNameError ? state.chatNameStatus : ""}
                />
              </CardContent>
            </Card>
            <p></p>
            <Card variant="outlined" style={{ margin: "2%", padding: "1%" }}>
              <CardContent>
                <Typography variant="subtitle1" color="primary">
                  Join Existing or Enter Room Name
                </Typography>

                <div>
                  <FormControl>
                    <RadioGroup
                      aria-labelledby="demo-radio-buttons-group-label"
                      name="radio-buttons-group"
                      onChange={radioGroupChange}
                      value={state.selectedRoom}
                    >
                      {state.rooms.filter(uniqueFilter).map((room) => (
                        <FormControlLabel
                          key={room}
                          value={room}
                          control={<Radio />}
                          label={room}
                        />
                      ))}
                    </RadioGroup>
                  </FormControl>
                </div>
                <TextField
                  onChange={onRoomChange}
                  placeholder="Enter room name"
                  required
                  value={state.roomName}
                  error={state.roomNameError}
                  helperText={state.roomNameError ? state.roomNameStatus : ""}
                />
                <p></p>
              </CardContent>
            </Card>
            <Button
              variant="contained"
              data-testid="submit"
              color="primary"
              style={{ margin: "3%", marginLeft: "5%", marginTop: "5%" }}
              onClick={() => handleJoin()}
              disabled={state.chatName === "" || state.roomName === ""}
            >
              Join
            </Button>
          </Card>
        </div>
      )}

      {!state.showjoinfields && state.messages && (
        <div className="messageList">
          <MessageBubbleList
            messages={state.messages}
            chatter={state.chatName}
          />
        </div>
      )}
      {!state.showjoinfields && (
        <TextField
          style={{ width: "90vw" }}
          onChange={onMessageChange}
          placeholder="type something here"
          autoFocus={true}
          value={state.message}
          onKeyPress={(e) => (e.key === "Enter" ? handleSendMessage() : null)}
        />
      )}
      <div>
        <Typography color="primary" style={{ textAlign: "right" }}>
          {state.typingMsg}
        </Typography>
      </div>
      <Dialog
        fullWidth
        maxWidth="sm"
        open={dialogOpen}
        onClose={handleCloseDialog}
      >
        <DialogTitle color="primary" style={{ textAlign: "center" }}>
          Who's On?
        </DialogTitle>
        <DialogContent>
          {state.users !== undefined
            ? state.users.map((user) => (
                <Typography
                  key={user.name}
                  style={{
                    verticalAlign: "middle",
                    display: "inline-flex",
                  }}
                >
                  <AccountBox
                    style={{
                      color: user.colour,
                      height: 20,
                      width: 20,
                      paddingRight: 20,
                    }}
                  />
                  {user.name} is in room {user.room}
                </Typography>
              ))
            : ""}
        </DialogContent>
      </Dialog>
    </ThemeProvider>
  );
};

export default Chatroom;
