import "./App.css";
const Bubble = (props) => {
  return (
    <div className="messageBubble" style={{ backgroundColor: props.colour }}>
      <span style={{ fontSize: 17, fontWeight: "bold" }}>
        {props.message.from}
      </span>
      <span style={{ color: "#a1a1a1" }}> @ {props.message.time} in </span>{" "}
      <span style={{ fontWeight: "bold" }}>{props.message.room}</span>
      <br />
      <div style={{ paddingLeft: 5, paddingTop: 1 }}>⌞{props.message.text}</div>
    </div>
  );
};
export default Bubble;
