import { useEffect, useRef } from "react";
import { ListItem } from "@mui/material";
import Bubble from "./bubble";
import Triangle from "./triangle";
const MessageBubble = (props) => {
  const messageRef = useRef(null);
  useEffect(() => {
    messageRef.current.scrollIntoView(true);
  }, []);
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      {props.message.from === props.chatter ? (
        <ListItem
          ref={messageRef}
          style={{
            marginLeft: "20%",
            marginBottom: "5%",
          }}
        >
          <Bubble message={props.message} colour={props.message.colour} />
          <Triangle colour={props.message.colour} alignTriangle={"57vw"} />
        </ListItem>
      ) : (
        <ListItem ref={messageRef} style={{ marginBottom: "5%" }}>
          <Bubble message={props.message} colour={props.message.colour} />

          <Triangle colour={props.message.colour} alignTriangle={"7vw"} />
        </ListItem>
      )}
      <p></p>
    </div>
  );
};
export default MessageBubble;
